import React, { useEffect, useState } from "react";
import { observer, inject } from "mobx-react";
import { Button, List, InputItem, Flex, WhiteSpace, WingBlank, Toast } from "antd-mobile";
import { EnumState } from "./constance";
import { getUserLargeIcon, getAccountIcon, getPasswordIcon } from "./service/getIconUrl";
import storage from "@/utils/storage";
import { setAuthorization } from "@/service/wechatAxiosInstance";
import Cookies from "js-cookie";
import socket from "@/pages/_internal/app/middlewares/AppAuthorized/websocket";

const Login = inject("userInfoStore")(
  observer((props: any) => {
    const {
      store,
      history,
      userInfoStore: { fetchUserInfo }
    } = props;

    // const DataApiService = store.DataApiService;
    const wechatAxiosInstance = store.wechatAxiosInstance;

    const searchMap = store.getSearchParams(history.location.search);
    const { clientToken, wxUser, wxCard, deviceId } = searchMap;

    // keep clientToken to session
    storage.setItem("client_token", clientToken);
    setAuthorization(clientToken);

    //const [submitState, setSubmitState] = useState(false);

    const onSubmitHandle = () => {
      if (!store.userName) {
        Toast.info("缺少用户名", 1);
        return;
      }
      if (!store.password) {
        Toast.info("缺少密码", 1);
        return;
      }

      let apiParam = null;
      if (wxUser && wxCard && clientToken) {
        apiParam = {
          wxUser: storage.getItem("wxUser"),
          clientToken: storage.getItem("client_token"),
          wxCard: storage.getItem("wxCard"),
          deviceId: deviceId || storage.getItem("deviceId")
        };
      } else {
        apiParam = {
          wxUser,
          wxCard,
          clientToken,
          deviceId
        };
      }

      store.submit(apiParam, (res: any) => {
        Cookies.remove("tokenId");
        Cookies.remove("userId");
        Cookies.remove("client_token");

        storage.clear();
        storage.setItem("wxUser", wxUser);
        storage.setItem("deviceId", deviceId);
        storage.setItem("userId", res.userId);
        storage.setItem("isBinding", res.isBinding);
        storage.setItem("tokenId", res.userToken);
        storage.setItem("userToken", res.userToken);
        storage.setItem("memberId", res.memberId);
        store.tokenContext.updateItemInToken("wxUser", wxUser);
        store.tokenContext.updateItemInToken("userId", res.userId);
        store.tokenContext.updateItemInToken("tokenId", res.userToken);
        fetchUserInfo();
        if (res.isBinding === 0) {
          history.push({
            pathname: "/user-binding",
            state: {
              clientToken,
              wxUser,
              wxCard,
              userId: res.userId,
              userToken: res.userToken,
              username: store.userName
            }
          });
        } else {
          history.push({
            pathname: "/dashboard",
            search: `userToken=${res.userToken}`
          });
        }
        socket.connect();
      });
    };

    return (
      <div className="loginContainer">
        <div className="title">
          <p>登录</p>
          <p>物联网管理平台</p>
        </div>
        <div className="inputDiv">
          <List>
            <InputItem className="inputName" placeholder="请输入账号" clear value={store.userName} onChange={store.userNameChange}>
              <img src={getAccountIcon()} />
            </InputItem>
            <InputItem
              className="inputPassword"
              clear
              type="password"
              placeholder="请输入密码"
              value={store.password}
              onChange={store.passwordChange}
            >
              <img src={getPasswordIcon()} />
            </InputItem>
          </List>
          <WhiteSpace size="xl" />
          <WhiteSpace size="xl" />
          <WingBlank size="lg">
            <Button onClick={onSubmitHandle} type="primary" style={{ backgroundColor: "#2ab6d9" }}>
              登录
            </Button>
          </WingBlank>
        </div>
      </div>
    );
  })
);

export default Login;
