import React, { useContext } from 'react';
import Store from './store';
import Login from './login';
import getSearchParams from '@/utils/tools/getSearchParams';
// import DataApiService from '@/service/http';
import wechatAxiosInstance from '@/service/wechatAxiosInstance';
import { TokenContext } from '@/pages/_internal/app/middlewares/AppAuthorized';
require('./style.scss');

export default (props: any) => {
    const tokenContext = useContext(TokenContext);
    const store = new Store({
        getSearchParams,
        wechatAxiosInstance,
        tokenContext,
    });
    return <Login store={store} {...props} />;
};
