import { observable, action } from "mobx";
import login from "@/service/http/authentication/login";
// mobx.configure({ enforceActions: "observed" }); // don't allow state modifications outside actions
import { EnumState } from "./constance";
// TODO 注入axios应该改为从login/index的传入
import { AxiosInstance } from "axios";
import { Toast } from "antd-mobile";

class Store {
  constructor(props: any) {
    this.getSearchParams = props.getSearchParams;
    // this.DataApiService = props.DataApiService;
    this.wechatAxiosInstance = props.wechatAxiosInstance;
    this.tokenContext = props.tokenContext;
  }
  tokenContext: IToken;
  getSearchParams: Function;
  // DataApiService: AxiosInstance;
  wechatAxiosInstance: AxiosInstance;
  @observable userName = "";
  @observable password = "";
  @observable response: any = {};
  @observable state: any = EnumState["noop"];
  @observable loading = false;
  @observable children: any = null;
  history: any = null;

  @action
  submit(param: any, callback: Function) {
    const { clientToken, wxUser, wxCard, deviceId } = param;
    const requestData = {
      username: this.userName,
      password: this.password,
      clientToken,
      wxCard,
      wxUser,
      deviceId
    };

    login(requestData)
      .then(res => {
        if (res && res.code === 0) {
          this.response = res.result;
          callback && callback(res.result);
        } else {
          if (res.message) {
            const errorIndex = res.message.lastIndexOf(":");
            const errorMess = res.message.substring(errorIndex + 1);
            Toast.fail(errorMess);
          }
        }
        this.clear();
      })
      .catch(err => {
        Toast.fail(err.message || "登录失败", 1);
      });
  }
  @action clear = () => {
    this.userName = "";
    this.password = "";
  };
  @action
  passwordChange = (value: any) => {
    this.password = value;
  };

  @action
  userNameChange = (value: any) => {
    this.userName = value;
  };

  @action
  getHistory = (history: any) => {
    this.history = history;
  };
}

export default Store;
